import React from 'react';
import { Link } from 'react-router-dom'
import { Flex, Box, Text } from 'rebass';
import { Toolbar, NavLink, Heading, Divider } from 'rebass';

const Menu = Toolbar.extend`
border-bottom:2px solid black;
margin-bottom:1.2rem
`

const Header = () => (
    <div>
        <Menu py={3} bg="white" color="black">
            <NavLink activeClassName='is-active' is={Link} to='/' fontSize={3}>shabd.xyz</NavLink>
            <NavLink activeClassName='is-active' px={[2, 3, null, 4]} ml='auto' is={Link} to='/'>Shabdkosh</NavLink>
            <NavLink activeClassName='is-active' px={[2, 3, null, 4]} is={Link} to='/create'>Create</NavLink>
            <NavLink activeClassName='is-active' px={[2, 3, null, 4]} is={Link} to='/about'>About</NavLink>
        </Menu>
    </div>

)
export default Header;


