import React, { Component } from 'react';
import { injectGlobal } from 'styled-components'
import { Provider, Heading, Container } from 'rebass';
import AppRouter from './AppRouter';
import './App.css';
injectGlobal`
  * { box-sizing: border-box; }
  body { margin: 0; }
  `
class App extends Component {
  render() {
    return (
      <Provider>
        <AppRouter />
      </Provider>
    );
  }
}

export default App;



