import React from 'react';
import { Heading, Input, Flex, Box, Textarea, ButtonCircle, Label, Divider, Container } from 'rebass';
export default class AddForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = { value: 'coconut' };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event) {
        alert('Your favorite flavor is: ' + this.state.value);
        event.preventDefault();
    }

    render() {
        return (
            <Container>
                <Flex flexWrap="wrap" flexDirection="column">
                    <Box width={[1, 2 / 3]}>
                        <Heading>Add Word or Meaning</Heading>
                        <form onSubmit={this.handleSubmit}>
                            <Label>
                                Hello Label
</Label>
                            <Input
                                defaultValue='Hello'
                                placeholder='Input'
                            />
                            <Label>
                                Hello Label
</Label>
                            <Textarea
                                rows={4}
                                defaultValue='Hello'
                            />
                            <Label>
                                Hello Label
                    </Label>
                            <Input
                                defaultValue='Hello'
                                placeholder='Input'
                            />
                            <ButtonCircle
                                children='Hello'
                            />
                        </form>
                    </Box>
                </Flex>
            </Container>
        );
    }
}


