import { Heading, Text, Container, Card, BackgroundImage, Subhead, Flex } from 'rebass'
import Word from './WordThumbnail';
const Homepage = (props) => (
    <div>
        <Container>
            <Flex p={4} flexWrap="wrap" justifyContent="space-between">
                <Word col="red" word="Pepsodent" meaning="Kya aapke toothpaste me namak hai bhaisahab, kya kehte hain aap?" /><Word /><Word /><Word /><Word /><Word col="yellow" /><Word /><Word />
            </Flex>
        </Container>
    </div>

);
export default Homepage;


