import React from 'react';
import { Card, Box, Text, Heading } from 'rebass';
const Word = (props) => (
    <Box width={[1, 1 / 3]} p={2} minHeight="400px" >
        <Card width={200} className="wordCard" bg={props.col}>
            <div class="wordCard__content">
                <Heading className="wordCard__word">{props.word || "Kya bolun"}</Heading>
                <Text className="wordCard__description">{props.meaning || "Kya bolun"}</Text>
            </div>
        </Card>
    </Box >
);
export default Word;


